}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, address, img, ol, ul, li, fieldset, form, label, article, footer, header, menu, nav, section {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

footer, header, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
