.btn-secondary, .btn-primary {
  text-transform: uppercase;
  text-align: center;
  height: 51px;
  min-width: 175px;
  cursor: pointer;
  border: 1px;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  padding: 16px 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.1875;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.btn-secondary:hover, .btn-primary:hover, .btn-secondary:focus, .btn-primary:focus {
  box-shadow: 0 8px 20px rgba(134, 229, 67, .5), 0 4px 10px rgba(255, 255, 255, .1);
}

