  line-height: 1.16667;
  display: block;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  max-width: 272px;
  z-index: 30;
  text-align: center;
  background-color: #01383a;
  border-radius: 3px;
  padding: 100px 30px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 688px;
    padding: 88px 40px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 768px;
  }
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 40px;
  height: 40px;
  fill: #86e543;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
